import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { ClarityModule } from 'clarity-angular';
import { AppComponent } from './app.component';
import { ROUTING } from "./app.routing";
import { SharedModule } from "./shared/shared.module";
import { LoginComponent } from './login/login.component';
import { NewBusinessComponent } from './business/business.component';
import { NewSightComponent } from './sight/sight.component';
import { NewOrganizationComponent } from './organization/organization.component';
import { ViewSightComponent } from './viewsight/viewsight.component';
import { ViewBusinessComponent } from './viewbusiness/viewbusiness.component';
import { ViewOrganizationComponent } from './vieworganization/vieworganization.component';
import { ViewPostComponent } from './viewpost/viewpost.component';
import { PostComponent } from './post/post.component';
import { AppState, INITIAL_APP_STATE } from './store/state';
import { StoreModule, combineReducers, ActionReducer } from "@ngrx/store";
import { EffectsModule } from '@ngrx/effects';
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { ImageUploadModule } from 'ng2-imageupload';
import { AgmCoreModule, MapsAPILoader } from '@agm/core';

import { UserReducer } from './store/reducers/userReducer';
import { ViewReducer } from './store/reducers/viewReducer';
import { DataReducer } from './store/reducers/dataReducer';
import { AppEffects } from './store/effects/appEffects';
import { AccessControlGuard } from './shared/guards/accesscontrol.service';
import { environment } from 'environments/environment';

const reducers = {
  user: UserReducer,
  view: ViewReducer,
  data: DataReducer
};

const effects = [
  EffectsModule.run(AppEffects)
];

const combinedReducers: ActionReducer<AppState> = combineReducers(reducers);

export function appReducers(state: AppState = INITIAL_APP_STATE, action: any) {
  return combinedReducers(state, action);
}

@NgModule({
    declarations: [
        AppComponent,
        LoginComponent,
        NewBusinessComponent,
        NewSightComponent,
        NewOrganizationComponent,
        ViewSightComponent,
        ViewBusinessComponent,
        ViewOrganizationComponent,
        PostComponent,
        ViewPostComponent
    ],
    imports: [
        BrowserAnimationsModule,
        BrowserModule,
        FormsModule,
        ReactiveFormsModule,
        HttpModule,
        ClarityModule.forRoot(),
        ROUTING,
        SharedModule,
        StoreModule.provideStore(appReducers),
        effects,
        AngularFireModule.initializeApp(environment.firebase),
        AngularFireDatabaseModule,
        AngularFireAuthModule,
        StoreDevtoolsModule.instrumentOnlyWithExtension({
          maxAge: 3
        }),
        AgmCoreModule.forRoot({
          apiKey: 'AIzaSyABiswPMWw1sZTcTxheEI1PStdF-Tup3Fw',
          libraries: ["places"]
        }),
        ImageUploadModule
    ],
    providers: [AccessControlGuard],
    bootstrap: [AppComponent]
})
export class AppModule {
}



