import { ActionReducer, Action } from "@ngrx/store";
import { DataState, INITIAL_DATA_STATE } from '../state';
import { AppActions } from "../actions/appActions";

export function DataReducer(state: DataState = INITIAL_DATA_STATE, action: Action) {
    // clones object for modification and return
    const newState: DataState = Object.assign({}, state);
    switch (action.type) {
        case AppActions.RECIEVED_CITY_ENTITIES:
          newState.cityEntities = action.payload;
          return newState;
        case AppActions.RECIEVED_CAMPUS_ENTITIES:
          newState.campusEntities = action.payload;
          return newState;
        case AppActions.RECIEVED_SIGHT_ENTITIES:
          newState.sightEntities = action.payload;
          return newState;
        case AppActions.SET_VIEW_SIGHT:
          newState.viewSight = action.payload;
          return newState;
        case AppActions.SET_VIEW_BUSINESS:
          newState.viewBusiness = action.payload;
          return newState;
        case AppActions.SET_VIEW_ORGANIZATION:
          newState.viewOrganization = action.payload;
          return newState;
        case AppActions.SET_NEW_POSTER:
          newState.newPoster = action.payload;
          return newState;
        case AppActions.SET_VIEW_POST:
          newState.viewPost = action.payload;
          return newState;
        default:
            return state;
    }
};


