import { Component, OnInit, NgZone, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Store } from '@ngrx/store';
import { AppActions } from '../store/actions/appActions';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { AppState, DataState } from '../store/state';
import { ImageResult, ResizeOptions } from 'ng2-imageupload';
import * as firebase from 'firebase';

@Component({
  selector: 'app-vieworganization',
  templateUrl: './vieworganization.component.html',
  styleUrls: ['./vieworganization.component.scss']
})
export class ViewOrganizationComponent implements OnInit {

  organization: any;
  query: string = '';
  posts: FirebaseListObservable<any[]>;

  logo: string = null;
  img1: string = null;
  img2: string = null;
  img3: string = null;

  options: ResizeOptions = {
        resizeMaxHeight: 128,
        resizeMaxWidth: 128
  };

  constructor(public db: AngularFireDatabase, private store: Store<AppState>, private router: Router) { }

  model: FormGroup = new FormGroup({
    name: new FormControl('', Validators.required),
    type: new FormControl('', Validators.required),
    ranking: new FormControl(1, Validators.required),
    description: new FormControl(''),
    category: new FormControl('', Validators.required),
    email: new FormControl('', [Validators.required, Validators.email]),
    website: new FormControl('', Validators.required)
  });

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state.data;
    }).subscribe((data: DataState) => {
        this.organization = data.viewOrganization;
        this.model.patchValue(data.viewOrganization);
        this.logo = this.organization.logo_url;
        this.img1 = this.organization.image1_url;
        this.img2 = this.organization.image2_url;
        this.img3 = this.organization.image3_url;
        this.posts = this.db.list('/school_posts/' + this.getCity(this.organization.school) + '/' + this.organization.school + '/', {query: { orderByChild: 'entity_id', equalTo: this.organization.$key}});
    });
  }

  submit(): void{
    if(this.model.valid){
      let submissionModel = {
        logo_url: this.logo || null,
        isOrg: true,
        image1_url: this.img1 || null,
        image2_url: this.img2 || null,
        image3_url: this.img3 || null,
        name: this.model.value.name,
        type: this.model.value.type,
        category: this.model.value.category,
        ranking: this.model.value.ranking,
        email: this.model.value.email,
        phone: this.organization.phone || null,
        website: this.model.value.website,
        zipcode: this.organization.zipcode || null,
        description: this.model.value.description
      };
      let info = this.db.list('/school_entities_info/' + this.getCity(this.organization.school) + '/' + this.organization.school + '/');
      info.update(this.organization.$key, submissionModel);
      let clientModel = {
        category: this.model.value.category,
        isOrg: true,
        logo_url: this.organization.logo_url || null,
        name: this.model.value.name,
        ranking: this.model.value.ranking,
        type: this.model.value.type
      };
      let client = this.db.list('/school_entities/' + this.getCity(this.organization.school) + '/' + this.organization.school + '/');
      client.update(this.organization.$key, clientModel);
      this.router.navigate(['/admin/campus']);
    }
  }

  selected(imageResult: ImageResult, dest:string): void {
    if (imageResult.error) alert(imageResult.error);
    this[dest] = imageResult.resized
      && imageResult.resized.dataURL
      || imageResult.dataURL;
    this.pushFileToStorage(imageResult, dest);
  }
  
  pushFileToStorage(imageResult: ImageResult, dest:string): void {
    const storageRef = firebase.storage().ref();
    const uuid = this.generateUUID();
    const uploadTask = storageRef.child(uuid + "_"  + imageResult.file.name).put(imageResult.file);
 
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (snapshot) => {
        const snap = snapshot as firebase.storage.UploadTaskSnapshot
      },
      (error) => {
        console.log(error)
      },
      () => {
        this[dest]=uploadTask.snapshot.downloadURL
      }
    );
  }

  generateUUID(): string {
    var d = new Date().getTime();
    var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = (d + Math.random()*16)%16 | 0;
        d = Math.floor(d/16);
        return (c=='x' ? r : (r&0x3|0x8)).toString(16);
    });
    return uuid;
  }

  qualify(entity: any): boolean {
    if(entity.type.toLowerCase().indexOf(this.query) > -1 || entity.name.toLowerCase().indexOf(this.query) > -1){
      return true;
    } else {
      return false;
    }
  }

  searchUpdate(value: any): void {
    if(value.target){
      this.query = value.target.value.toLowerCase();
    } else {
      this.query = value.toLowerCase();
    }
  }

  getCity(school: string): string {
    if(school == 'UC San Diego') {
      return 'San Diego';
    } else if(school == 'UC Santa Barbara'){
      return 'Santa Barbara';
    } else if(school == 'UC Davis'){
      return 'Davis';
    }
  }

  logout(): void {
    this.store.dispatch({type: AppActions.LOGOUT, payload: null});
    this.router.navigate(['login']);
  }

  newPost(): void {
    this.store.dispatch({type: AppActions.SET_NEW_POSTER, payload: this.organization});
    this.router.navigate(['/post']);
  }

  cancel(): void{
    this.router.navigate(['/admin/campus']);
  }

  edit(post: any): void {
    this.store.dispatch({type: AppActions.SET_VIEW_POST, payload: post});
    this.router.navigate(['/viewpost']);
  }
}


