import { Users } from "../types";
import { FirebaseListObservable } from 'angularfire2/database';


export interface UserState {
  loggedIn: boolean;
  loginFail: boolean;
  userData: Users;
}

export const INITIAL_USER_STATE: UserState = {
  loggedIn: false,
  loginFail: false,
  userData: null
}

export interface ViewState {
	service: string;
	city: string;
	school: string;
}

export const INITIAL_VIEW_STATE: ViewState = {
	service: 'CityLive',
	city: 'San Diego',
	school: 'UC San Diego'
}

export interface DataState {
  cityEntities: FirebaseListObservable<any[]>;
  campusEntities: FirebaseListObservable<any[]>;
  sightEntities: FirebaseListObservable<any[]>;
  viewSight: any;
  viewBusiness: any;
  viewOrganization: any;
  viewPost: any;
  newPoster: any;
}

export const INITIAL_DATA_STATE: DataState = {
  cityEntities: null,
  campusEntities: null,
  sightEntities: null,
  viewSight: null,
  viewBusiness: null,
  viewOrganization: null,
  newPoster: null,
  viewPost: null
}

export interface AppState {
  user: UserState;
  view: ViewState;
  data: DataState;
}

export const INITIAL_APP_STATE: AppState = {
   user: INITIAL_USER_STATE,
   view: INITIAL_VIEW_STATE,
   data: INITIAL_DATA_STATE
}


