import { ModuleWithProviders } from '@angular/core/src/metadata/ng_module';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { NewBusinessComponent } from './business/business.component';
import { AdminComponent } from './admin/admin.component';
import { NewOrganizationComponent } from './organization/organization.component';
import { NewSightComponent } from './sight/sight.component';
import { ViewBusinessComponent } from './viewbusiness/viewbusiness.component';
import { ViewOrganizationComponent } from './vieworganization/vieworganization.component';
import { PostComponent } from './post/post.component';
import { ViewPostComponent } from './viewpost/viewpost.component';
import { ViewSightComponent } from './viewsight/viewsight.component';

import { AccessControlGuard } from './shared/guards/accesscontrol.service';


export const ROUTES: Routes = [
    {path: '', redirectTo: 'login', pathMatch: 'full'},
    {path: 'login', component: LoginComponent},
    {path: 'admin', loadChildren: './admin/admin.module#AdminModule', canActivate: [AccessControlGuard]},
    {path: 'business', component: NewBusinessComponent, canActivate: [AccessControlGuard]},
    {path: 'sight', component: NewSightComponent, canActivate: [AccessControlGuard]},
    {path: 'organization', component: NewOrganizationComponent, canActivate: [AccessControlGuard]},
    {path: 'viewsight', component: ViewSightComponent, canActivate: [AccessControlGuard]},
    {path: 'viewbusiness', component: ViewBusinessComponent, canActivate: [AccessControlGuard]},
    {path: 'vieworganization', component: ViewOrganizationComponent, canActivate: [AccessControlGuard]},
    {path: 'post', component: PostComponent, canActivate: [AccessControlGuard]},
    {path: 'viewpost', component: ViewPostComponent, canActivate: [AccessControlGuard]}
];

export const ROUTING: ModuleWithProviders = RouterModule.forRoot(ROUTES);


