import { ActionReducer, Action } from "@ngrx/store";
import { ViewState, INITIAL_VIEW_STATE } from '../state';
import { AppActions } from "../actions/appActions";

export function ViewReducer(state: ViewState = INITIAL_VIEW_STATE, action: Action) {
    // clones object for modification and return
    const newState: ViewState = Object.assign({}, state);
    switch (action.type) {
        case AppActions.CHANGE_SERVICE:
          newState.service = action.payload;
          return newState;
        case AppActions.CHANGE_SCHOOL:
          newState.school = action.payload;
          return newState;
        case AppActions.CHANGE_CITY:
          newState.city = action.payload;
          return newState;
        default:
            return state;
    }
};


