import { Component, OnInit, NgZone, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Store } from '@ngrx/store';
import { AppActions } from '../store/actions/appActions';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { AppState, DataState } from '../store/state';
import { AgmCoreModule, MapsAPILoader } from '@agm/core';
import { AgmMap } from '@agm/core';
import { ImageResult, ResizeOptions } from 'ng2-imageupload';
import * as firebase from 'firebase';

@Component({
  selector: 'app-viewbusiness',
  templateUrl: './viewbusiness.component.html',
  styleUrls: ['./viewbusiness.component.scss']
})
export class ViewBusinessComponent implements OnInit {

  business: any;
  latitude: number = 39.8282;
  longitude: number = -98.5795;
  query: string = '';
  zoom: number = 12;
  posts: FirebaseListObservable<any[]>;

  logo: string = null;
  img1: string = null;
  img2: string = null;
  img3: string = null;

  options: ResizeOptions = {
        resizeMaxHeight: 128,
        resizeMaxWidth: 128
  };


  @ViewChild('agmMap') agmMap : AgmMap;

  constructor(public db: AngularFireDatabase, private store: Store<AppState>, private router: Router, private ngZone: NgZone) { }

  model: FormGroup = new FormGroup({
    name: new FormControl('', Validators.required),
    type: new FormControl('', Validators.required),
    ranking: new FormControl(1, Validators.required),
    description: new FormControl(''),
    category: new FormControl('', Validators.required)
  });

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state.data;
    }).subscribe((data: DataState) => {
        this.business = data.viewBusiness;
        this.model.patchValue(data.viewBusiness);
        this.ngZone.run(() => {
          this.longitude = data.viewBusiness.longitude;
          this.latitude = data.viewBusiness.latitude;
        });
        this.logo = this.business.logo_url;
        this.img1 = this.business.image1_url;
        this.img2 = this.business.image2_url;
        this.img3 = this.business.image3_url;
        this.posts = this.db.list('/city_posts/' + this.business.city, {query: { orderByChild: 'entity_id', equalTo: this.business.$key}});
    });
  }

  selected(imageResult: ImageResult, dest:string): void {
    if (imageResult.error) alert(imageResult.error);
    this[dest] = imageResult.resized
      && imageResult.resized.dataURL
      || imageResult.dataURL;
    this.pushFileToStorage(imageResult, dest);
  }

  setAddress(place: any): void {
    this.ngZone.run(() => {
      this.latitude = place.geometry.location.lat();
      this.longitude = place.geometry.location.lng();
      this.zoom = 12;
    });
  }

  markerDragEnd(m: any, $event: any): void {
    this.latitude = $event.coords.lat;
    this.longitude = $event.coords.lng;
  }

  pushFileToStorage(imageResult: ImageResult, dest:string): void {
    const storageRef = firebase.storage().ref();
    const uuid = this.generateUUID();
    const uploadTask = storageRef.child(uuid + "_"  + imageResult.file.name).put(imageResult.file);
 
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (snapshot) => {
        const snap = snapshot as firebase.storage.UploadTaskSnapshot
      },
      (error) => {
        console.log(error)
      },
      () => {
        this[dest]=uploadTask.snapshot.downloadURL
      }
    );
  }

  generateUUID(): string {
    var d = new Date().getTime();
    var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = (d + Math.random()*16)%16 | 0;
        d = Math.floor(d/16);
        return (c=='x' ? r : (r&0x3|0x8)).toString(16);
    });
    return uuid;
  }

  submit(): void{
    if(this.model.valid){
      let submissionModel = {
        logo_url: this.logo || null,
        longitude: this.longitude,
        latitude: this.latitude,
        image1_url: this.img1  || null,
        image2_url: this.img2  || null,
        image3_url: this.img3  || null,
        name: this.model.value.name,
        type: this.model.value.type,
        ranking: this.model.value.ranking,
        zipcode: this.business.zipcode || null,
        description: this.model.value.description,
        email: this.business.email || null,
        phone: this.business.phone || null,
        website: this.business.website || null,
        category: this.model.value.category,
        businessHours: this.business.businessHours || null,
        irOrg: false
      };
      let info = this.db.list('/city_entities_info/' + this.business.city + '/');
      info.update(this.business.$key, submissionModel);
      let clientModel = {
        category: this.model.value.category,
        isOrg: false,
        latitude: this.latitude,
        logo_url: this.business.logo_url || null,
        longitude: this.longitude,
        name: this.model.value.name,
        ranking: this.model.value.ranking,
        type: this.model.value.type,
        businessHours: this.business.businessHours || null
      };
      let client = this.db.list('/city_entities/' + this.business.city + '/');
      client.update(this.business.$key, clientModel);
      this.router.navigate(['/admin']);
    }
  }

  qualify(entity: any): boolean {
    if(entity.type.toLowerCase().indexOf(this.query) > -1 || entity.name.toLowerCase().indexOf(this.query) > -1){
      return true;
    } else {
      return false;
    }
  }

  searchUpdate(value: any): void {
    if(value.target){
      this.query = value.target.value.toLowerCase();
    } else {
      this.query = value.toLowerCase();
    }
  }

  logout(): void {
    this.store.dispatch({type: AppActions.LOGOUT, payload: null});
    this.router.navigate(['login']);
  }

  newPost(): void {
    this.store.dispatch({type: AppActions.SET_NEW_POSTER, payload: this.business});
    this.router.navigate(['/post']);
  }

  cancel(): void{
    this.router.navigate(['/admin']);
  }

  edit(post: any): void {
    this.store.dispatch({type: AppActions.SET_VIEW_POST, payload: post});
    this.router.navigate(['/viewpost']);
  }
}


